/**常规定位的简写**/
.re {
    position: relative;
}
.ab {
    position: absolute;
}
// 撑满整个屏幕  老版命名映射: PhoneScreen
.page-screen {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
}
.page-screen-center {
    width: 100vw;
    height: 100vh;
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

// 公共的页面样式 老版命名映射: PublicScenes
.public-page-style {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
}
// 老版命名映射: Content
.public-content-style {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    justify-content: center;
    align-content: center;
    overflow: hidden;
}
.public-content-style-center {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
// 公共的浮层样式 老版命名映射: Floating
.public-panel-style {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.6);
}
// 对内容进行居中 - 内容需要最好是有 relative
.public-center-conetnt {
    width: 100vw;
    height: 100vh;
    display: grid;
    justify-content: center;
    align-content: center;
    overflow: hidden;
}
/* 隐藏滚动条 */
.element {
    width: 100%;
    height: 72px;
    box-sizing: border-box;
    overflow-x: scroll; /* 设置溢出滚动 */
    white-space: nowrap;
    overflow-y: hidden;
    /* 隐藏滚动条 */
    scrollbar-width: none; /* firefox */
    -ms-overflow-style: none; /* IE 10+ */
}
.element::-webkit-scrollbar {
    display: none; /* Chrome Safari */
}
.overflow-fill-point {
    overflow: hidden; /* 超出一行文字自动隐藏 */
    text-overflow: ellipsis; /* 文字隐藏后添加省略号 */
    white-space: nowrap; /* 强制文本不换行 */
}
